.wishlist
  display: none
  @media (min-width: 768px)
    display: block
    width: 20px
    height: 20px
    margin-left: 40px
    cursor: pointer
    position: relative
    svg
      stroke: var(--umbra)
      width: 100%
      height: 100%
      transition: all 0.3s ease
      &:hover
        stroke: var(--taffy-pink)
  @media (min-width: 1000px)
    margin-left: 50px
  @media (min-width: 1440px)
    width: 24px
    height: 24px
    margin-left: 115px

.active svg
  stroke: var(--taffy-pink)

.counter
  position: absolute
  top: -5px
  right: -8px
  display: flex
  align-items: center
  justify-content: center
  width: 16px
  height: 16px
  font-size: 9px
  font-weight: var(--medium-fw)
  color: var(--white)
  background-color: var(--red)
  border-radius: 50%
  @media (min-width: 1000px)
    top: -6px
    right: -11px
    width: 18px
    height: 18px
    font-size: 10px
  @media (min-width: 1440px)
    top: -8px
    right: -12px
    width: 20px
    height: 20px
    font-size: 11px
